var NetwCookieKeys = require('../constants/NetwCookieConstants');
var NetwWebAPIUtils = require('./NetwApiCall');
var NetwCookie = require('./NetwCookies');

var _host = window._netwHost;

module.exports = {
    parseUrlFormatByName: function(content, name) {
        name = name.replace(/[\[]/, "\\[").replace(/[\]]/, "\\]");
        var regex = new RegExp("[\\?&]" + name + "=([^&#]*)"),
            results = regex.exec(content);
        return results === null
            ? ""
            : decodeURIComponent(results[1].replace(/\+/g, " "));
    },
    parseUrlFormatByNameIC: function(content, name) {
        name = name.replace(/[\[]/, "\\[").replace(/[\]]/, "\\]");
        var regex = new RegExp("[\\?&]" + name + "=([^&#]*)", "gi"),
            results = regex.exec(content);
        return results === null
            ? ""
            : decodeURIComponent(results[1].replace(/\+/g, " "));
    },
    isHttpUriAbsolute: function(uri) {
        var result = false;
        if (uri.indexOf('http://') === 0 || uri.indexOf('https://') === 0) {
            result = true;
        }
        return result;
    },
    isUriAbsolute: function(uri) {
        var result = false;
        var absoluteUrl = new RegExp('^(?:[a-z]+:)?//', 'i');
        result = absoluteUrl.test(uri);
        return result;
    },
    isSecure: function() {
        if (window.location.protocol != "https:") {
            return false;
        } else {
            return true;
        }
    },
    isLogin: function() {
        var path = window._netwHost + '/api/auchk';
        var opt = {
            url: path,
            async: true,
            cache: false,
            dataType: 'json',
            contentType: 'application/x-www-form-urlencoded; charset=UTF-8',
            crossDomain: true,
            headers: {},
            customAuthorization: false
        };
        var neui = new NetwCookie(NetwCookieKeys.CookieKeys.NETW_USER, null);
        var _neui = neui.get();
        if (!_neui) {
            return new Promise(function(resolve, reject) {
                resolve(false);
            });
        }

        var netwApi = new NetwWebAPIUtils(opt);
        return netwApi.get();
    },
    getEamil: function() {
        var neui = new NetwCookie(NetwCookieKeys.CookieKeys.NETW_USER, null);
        var _neui = neui.get();
        if (!_neui) {
            return "";
        }
        var email = this.parseUrlFormatByName(_neui, "mail");
        return email;
    },
    matchEmail: function(email) {
        var emailRule = /^([\w-\.\+\-\_]+)@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.)|(([\w-]+\.)+))([a-zA-Z]{2,4})$/;
        return email.match(emailRule);
    }
};



